.listEventServices__h1{
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 0;
    padding: .5rem 0 .5rem 1rem;
    color: lighten($color: $azul_gris, $amount: 15%);
    background-color: lighten($color: $secundario, $amount: 15%);


    font-size: 2rem;
    &:not(:first-child){
        margin-top: 5rem;
    }

}
.cardHorizontal{
    width: 70%;
    margin: 0 auto;
}
.cardHorizontal__auxImgInfoEvento{
    
    @include mqTelefono{
        // height: 20rem;
        // width: 50%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);

    }
    .cardHorizontal__container{
        height: 20rem;
    }
    .cardHorizontal__container--img{
        display: block;
        overflow: hidden;
        background-color: lighten($color: $secundario, $amount: 55%);
        padding: 1rem;
        .img-evento{
            width: 100%;
            height: 100%;

            display: block;
            .pic-img{
                width: 100%;
                height: 100%;
                object-fit:cover;
            }
        }
    }

    .cardHorizontal__containerInfoEvento{
        background-color: lighten($color: $secundario, $amount: 15%);
        display: flex;
        .info-evento{
        display: flex;

            width: 100%;
            margin: 0;
            padding: 0;
            border: 0;
            margin: auto 0;

            .infoEvento__tabla{
                margin: auto;
            }
        }
    }

}

